<template>
  <div class="employmaid common">
    <el-image :style="{width:'100%'}" :src="style.src_pic"></el-image>

    <!-- 雇佣女佣流程线开始 -->
    <el-row class="flowLines" :gutter="0">
      <el-col :span="3">
        <p>&nbsp;</p>
      </el-col>
      <el-col :span="18" class="flowLinesCol">
        <el-steps :active="1">
          <el-step title="雇佣要求" description status="finish"></el-step>
          <el-step title="遴选" description status="wait"></el-step>
          <el-step title="菲佣面试" description status="wait"></el-step>
          <el-step title="签订合同" description status="wait"></el-step>
          <el-step title="合同终止" description status="wait"></el-step>
        </el-steps>
      </el-col>
      <el-col :span="3">
        <p>&nbsp;</p>
      </el-col>
    </el-row>
    <!-- 雇佣女佣流程线结束 -->

    <!-- 需求部分开始 -->
    <el-row class="requirement" :gutter="0">
      <el-col :span="3">
        <p>&nbsp;</p>
      </el-col>
      <el-col :span="18" class="requirementCol">
        <el-row class="requirement_text">要求：</el-row>
        <el-row>
          <el-form ref="form" :model="form" label-width="80px">
            <el-col :lg="8" :md="10">
              <el-form-item label="经验年限">
                <el-select v-model="form.workExp" placeholder="请选择">
                  <el-option
                    :label="item1"
                    :value="item1"
                    v-for="(item1,index1) in module_all.WorkExp"
                    :key="index1 + 'a'"
                  ></el-option>
                </el-select>
              </el-form-item>
              <el-form-item label="年龄范围">
                <el-select v-model="form.ageRange" placeholder="请选择">
                  <el-option label="20 - 25" value="shanghai"></el-option>
                  <el-option label="25 - 30" value="beijing"></el-option>
                  <el-option label="30 - 40" value="beijing"></el-option>
                </el-select>
              </el-form-item>
              <el-form-item label="教育背景">
                <el-select v-model="form.education" placeholder="请选择">
                  <el-option
                    :label="item3"
                    :value="item3"
                    v-for="(item3,index3) in module_all.Education"
                    :key="index3 + 'c'"
                  ></el-option>
                </el-select>
              </el-form-item>
            </el-col>

            <el-col :lg="14" :md="14">
              <el-form-item label="海外工作经验" class="overseasExp">
                <el-radio-group v-model="form.overseasExp">
                  <el-radio label="需要"></el-radio>
                  <el-radio label="不需要"></el-radio>
                </el-radio-group>
              </el-form-item>

              <el-col>
                <el-form-item label="技能要求" class="skill_lable">
                  <el-col>
                    <el-checkbox-group v-model="form.skill">
                      <el-row>
                        <el-checkbox label="护理小孩" name="type"></el-checkbox>
                        <el-checkbox label="护理老人" name="type"></el-checkbox>
                        <el-checkbox label="驾驶" name="type"></el-checkbox>
                      </el-row>
                      <el-row>
                        <el-checkbox label="照顾残障" name="type"></el-checkbox>
                        <el-checkbox label="婴儿护理" name="type"></el-checkbox>
                        <el-checkbox label="烹饪" name="type"></el-checkbox>
                      </el-row>
                      <el-row>
                        <el-checkbox label="照顾孕妇" name="type"></el-checkbox>
                        <el-checkbox label="照顾卧床病人" name="type"></el-checkbox>
                      </el-row>
                      <el-row>
                        <el-checkbox label="照顾宠物" name="type"></el-checkbox>
                        <el-checkbox label="作业辅导" name="type"></el-checkbox>
                      </el-row>
                    </el-checkbox-group>
                  </el-col>
                </el-form-item>
              </el-col>
            </el-col>
          </el-form>
        </el-row>
      </el-col>
      <el-col :span="3">
        <p>&nbsp;</p>
      </el-col>
    </el-row>
    <!-- 需求部分结束 -->

    <!-- 最后匹配部分开始 -->

    <el-row class="matching">
      <el-col :span="3">
        <p>&nbsp;</p>
      </el-col>
      <el-col :span="18">
        <el-row class="padding_row last_padding_row">
          <el-col :span="6">&nbsp;</el-col>
          <el-col :span="12">
            <el-col :span="8">
              <el-button size="medium" type="primary" class="matching_btn">匹配</el-button>
            </el-col>
            <el-col :span="8">&nbsp;</el-col>
            <el-col :span="8">
              <el-button size="medium" class="reset_btn" @click="resetData()">重置</el-button>
            </el-col>
          </el-col>
          <el-col :span="6">&nbsp;</el-col>
        </el-row>
      </el-col>
      <el-col :span="3">
        <p>&nbsp;</p>
      </el-col>
    </el-row>
    <!-- 最后匹配部分结束  -->

    <div class="body_bottom color_bg"></div>
  </div>
</template>

<script>
// import "@/styles/employmaid.css";
// import "@/styles/common.css";
import { mapGetters } from "vuex";

export default {
  name: "EmployMaid",
  components: {},
  computed: {
    ...mapGetters(["module_all"])
  },
  data() {
    return {
      style: {
        src_pic: require("@/assets/maid/pic (3).png"),
      },
      form: {
        name: '',
        workExp: null,
        ageRange: null,
        education: null,
        overseasExp: null,
        skill: [],
      }
    };
  },
  mounted() {
    console.log(this.module_all);
  },
  methods: {
    resetData() {
      this.form.workExp = null
      this.form.ageRange = null
      this.form.education = null
      this.form.overseasExp = null
      this.form.skill = []
    }
  }
};
</script>

<style scoped>
.requirement {
  background: #fafafa;
}
.matching {
  height: 220px;
  line-height: 220px;
  text-align: center;
}
.matching .matching_btn {
  margin-right: 200px;
}
/* .matching .reset_btn,
.matching .matching_btn {
  width: 180px;
  height: 45px;
  font-size: 16px;
} */
.requirementCol .requirement_text {
  margin: 40px 0px;
  font-size: 20px;
}
.matching .padding_row .matching_btn,
.matching .padding_row .reset_btn {
  width: 100%;
}
</style>

<style>
/* 雇佣女佣流程线开始 */
.flowLines {
  background: #fff;
}
.flowLines .flowLinesCol {
  padding: 90px 0px;
}
.flowLines .flowLinesCol .el-step__icon {
  width: 45px;
  height: 45px;
  font-size: 24px;
  font-weight: normal;
}
.flowLines .flowLinesCol .el-step__icon-inner {
  font-weight: normal;
}
.flowLines .flowLinesCol .el-step__title {
  font-size: 20px;
}
.flowLines .flowLinesCol .el-step.is-horizontal .el-step__line {
  top: 21px;
}
/* 雇佣女佣流程线结束 */

.requirementCol .el-form-item__label {
  text-align: left;
}
.requirementCol .overseasExp label.el-form-item__label,
.requirementCol .skill_lable label.el-form-item__label {
  width: 120px !important;
}
.requirementCol .overseasExp .el-form-item__content {
  line-height: 44px;
}
.requirementCol .skill_lable .el-form-item__content {
  margin-left: 120px !important;
}
.requirementCol .skill_lable .el-checkbox__label,
.requirementCol .overseasExp .el-radio__label {
  color: #777 !important;
}
.requirement .el-form label.el-form-item__label {
  font-size: 15px !important;
  color: #333;
}
</style>



